import loginTemp from '@/components/login'
import inputCell from '@/components/input-cell'
import { dealCountDown, verifyPhoneNumber } from '@/common/js/common.js'
import { register } from '@/api'
import { mapActions } from 'vuex'
export default {
	name: '',
	data() {
		return {
			phone: '',
			pwd: '',
			sms: '',
			count: 0
		}
	},
	onLoad() {

	},
	methods: {
		...mapActions(['reqGetSms']),
		onGetSmsEvent () {
			var that = this;
			if(!verifyPhoneNumber(that.phone)) {
				return;
			}
			this.reqGetSms({phone: this.phone, callback: dealCountDown, that: that})
		},
		async onRegisterEvent () {
			var that = this;
			if(!verifyPhoneNumber(that.phone)) {
				return;
			}
			if(this.sms == "") {
				uni.showToast({
					title: '请输入验证码',
					icon: 'none'
				})
				return;
			}
			if(this.pwd == "") {
				uni.showToast({
					title: '请输入密码',
					icon: 'none'
				})
				return;
			}
			let result = await register ({
				mobile: this.phone,
				password: this.pwd,
				code: this.sms
			})
			if(result.code == 200) {
				uni.showToast({
					title: '注册成功',
				})
				setTimeout( () => {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 2000)
			}
		}
	},
	components: {
		loginTemp,
		inputCell
	}
}
